<template>
  <div style="border: solid 1px #999; width: 100%">

    <div style="width: 100%; background-color: #255d9a;
    color: white; font-size: 23px;
    padding-top: 10px; padding-bottom: 10px;">
      <span style="margin: 5px">商品列表</span>
    </div>
    <div style="width: 100%; background-color: #eee;">
      <div style="padding: 5px">
        <el-button size="mini" type="text" icon="el-icon-plus">添加</el-button>
        <el-button size="mini" type="text" icon="el-icon-edit">编辑</el-button>
        <el-button size="mini" type="text" icon="el-icon-delete">删除</el-button>
        <el-button size="mini" type="text" icon="el-icon-delete">下架</el-button>
        <el-button size="mini" type="text" icon="el-icon-delete">上架</el-button>
      </div>
    </div>
    <div style="margin: 10px">
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        border
        size="mini"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          label="商品ID"
          width="120"
          prop="id"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="title"
          label="商品标题"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="cid"
          label="叶子类目"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="sellPoint"
          label="卖点"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="price"
          label="价格"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="num"
          label="库存数量"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="address"
          label="条形码"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="created"
          :formatter="toDate"
          label="创建日期"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="updated"
          :formatter="toDate"
          label="更新日期"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
    </div>
    <div class="block" style="margin: 10px">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import {formatDate} from '../../util/TimeUtil'
  export default {
    created(){
      this.initData();
    },
    data() {
      return {
        currentPage:1,
        tableData: [{
          barcode: "",
          cid: 0,
          created: 0,
          id: 0,
          image: "",
          num: 0,
          price: 0,
          sellPoint: "",
          status: 1,
          title: "",
          updated: 0
        }],
        multipleSelection: [],
        total: 0
      }
    },
    methods: {
      initData() {
        this.$http({
          method: 'GET',
          url: 'http://192.168.57.49:8087/item/list?page=1&rows=10'
        }).then(response => {
          console.log(response.body);
          this.tableData = response.body.rows;
          this.total = response.body.total
        }, error => {
          this.$message.error('网络有问题!');
        });
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.$http({
          method: 'GET',
          url: 'http://192.168.57.49:8087/item/list',
          params: {
            page: val,
            rows: 10
          }
        }).then(response => {
          console.log(response.body);
          this.tableData = response.body.rows;
          this.total = response.body.total
        }, error => {
          this.$message.error('网络有问题!');
        });
      },
      toDate(row, column, cellValue) {
        const date = new Date(row.created);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
  };
</script>

<style scoped>
</style>



