<template>
  <div>
    <section>
      <figure class="upload-preview">
        <img :src="thumbnail" v-if="thumbnail" height="90"/>
      </figure>
      <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
      <a>选择图片文件<i class="progress" :style="{width: progress + '%'}"></i></a>
      </upload>
    </section>
  </div>

</template>
<script>
  import upload from './common/upload.vue'
  export default {
    components : {
      upload,
    },
    data () {
      return {
        thumbnail : null,
        progress : 0 //上传进度
      }
    },
    methods : {
      uploadProgress (progress, flag) {
        //这里可以通过回调的flag对不同上传域做处理
        console.log(progress);
        this.progress = progress < 100 ? progress : 0;
      },
      uploadComplete(status, result, flag) {
        if (status === 200) { //
          this.thumbnail = `http://ojx7gsigh.bkt.clouddn.com/${result.key}` //七牛域名 + 返回的key 组成文件url
        } else {
          //失败处理
          console.log('失败');
        }
      },
    }
  }
</script>
<style>
  .progress {
    background-color: darkcyan;
    width: 300px;
    height: 20px;
  }
</style>



