import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '../components/HelloWorld.vue'
import GoodsIndex from '../components/page/GoodsIndex.vue';
import AddGoods from '../components/page/AddGoods.vue';
import test from '../components/test.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/GoodsIndex',
      name: 'GoodsIndex',
      component: GoodsIndex
    },
    {
      path: '/AddGoods',
      name: 'AddGoods',
      component: AddGoods
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
  ]
})



