<template>
  <div style="border: solid 1px #999; width: 100%">
    <div style="width: 100%; background-color: #255d9a;
    color: white; font-size: 23px;
    padding-top: 10px; padding-bottom: 10px;">
      <span style="margin: 5px">添加商品</span>
    </div>

    <div style="margin-top: 20px">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" labelWidth="100px" class="demo-ruleForm">
        <el-form-item label="商品类目" prop="cid">
          <el-button size="mini" type="text" icon="el-icon-plus" @click="showDialog">选择类目</el-button>
          {{category + ruleForm.cid}}
        </el-form-item>
        <el-form-item label="商品标题" prop="title">
          <el-input style="width: 300px" v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="商品卖点" prop="sellPoint">
          <el-input style="width: 300px" v-model="ruleForm.sellPoint"></el-input>
        </el-form-item>
        <el-form-item label="商品价格" prop="price">
          <el-input style="width: 100px" v-model="ruleForm.price"></el-input>
        </el-form-item>
        <el-form-item label="库存数量" prop="num">
          <el-input style="width: 100px" v-model="ruleForm.num"></el-input>
        </el-form-item>
        <el-form-item label="条形码" prop="barcode">
          <el-input style="width: 200px" v-model="ruleForm.barcode"></el-input>
        </el-form-item>
        <el-form-item label="商品图片" prop="image">
          <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
            <a>选择图片文件<i class="progress" :style="{width: progress + '%'}"></i></a>
          </upload>
          <div>
            <img style="margin: 5px" v-for="url in thumbnail" :src="url" v-if="url" height="60"/>
          </div>
        </el-form-item>
        <el-form-item label="商品描述" prop="desc">
          <div class="editor-container">
            <UE :defaultMsg=defaultMsg :config=config ref="ue"></UE>
          </div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-dialog title="收货地址" :visible.sync="dialogTableVisible">
      <el-tree
        :props="props"
        :load="loadNode"
        lazy>
      </el-tree>
    </el-dialog>

    <el-button type="primary" @click="postRequest">POST提交</el-button>

  </div>
</template>
<script>
  import upload from '../common/upload.vue'
  import UE from '../common/uedit.vue';
  export default {
    components: {
      upload,
      UE
    },
    data() {
      return {
        defaultMsg: '请编写商品详情',
        config: {
          initialFrameWidth: 750,
          initialFrameHeight: 350
        },
        thumbnail : [],
        progress: 0,
        props: {
          label: 'text',
          children: 'zones',
          isLeaf: 'leaf'
        },
        fileList2: [],
        category: '',
        gridData: [],
        dialogTableVisible: false,
        ruleForm: {
          title: '',
          sellPoint: '',
          price: 0,
          num: 0,
          barcode: '',
          image: '',
          cid: '',
          desc: ''
        },
        rules: {
          title: [
            {required: true, message: '请输入商品名称', trigger: 'blur'}
          ],
          sellPoint: [
            {required: true, message: '请输入卖点', trigger: 'blur'}
          ],
        },
        cId: 0
      };
    },
    methods: {
      uploadComplete(status, result, flag) {
        if (status === 200) { //
          this.thumbnail.push(`http://ojx7gsigh.bkt.clouddn.com/${result.key}`);
          this.ruleForm.image = `http://ojx7gsigh.bkt.clouddn.com/${result.key}`;
        } else {
          //失败处理
          console.log('失败');
        }
      },
      uploadProgress(progress, flag) {
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.ruleForm.desc = this.$refs.ue.getUEContent();
            const _this = this;
            _this.$http({
              method: 'POST',
              url: 'http://192.168.57.49:8087/item/save',
              body: {
                title: _this.ruleForm.title,
                sellPoint: _this.ruleForm.sellPoint,
                price: _this.ruleForm.price,
                num: _this.ruleForm.num,
                barcode: _this.ruleForm.barcode,
                image: _this.ruleForm.image,
                cid: _this.ruleForm.cid,
                desc: _this.ruleForm.desc
              }
            }).then(response => {
              console.log(response.body);
            }, error => {
              this.$message.error('网络有问题!');
            });
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      showDialog() {
        this.dialogTableVisible = true;
      },
      loadNode(node, resolve) {
        console.log(node);
        let id=0;
        if (node.data) {
          id = node.data.id;
          if (node.data.state === 'open') {
            this.category = node.data.text;
            this.dialogTableVisible = false;
          }
        }

        this.$http({
          method: 'GET',
          url: 'http://192.168.57.49:8087/item/cat/list',
          params: {
            id: id,
          }
        }).then(response => {
          let body = response.body;
          resolve(body);
        }, error => {
          this.$message.error('网络有问题!');
        });
      },
      postRequest() {
        const _this = this;
        _this.$http({
          method: 'POST',
          url: 'http://192.168.57.49:8087/item/test',
          body: {
            title: 'aaa',
          },
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).then(response => {
          console.log(response.body);
        }, error => {
          this.$message.error('网络有问题!');
          console.log(error);
        });
      }
    }
  };
</script>

<style scoped>
  .editor-container{
    width: 750px;
  }
</style>



