<template>
  <div class="content">
    <el-container>
      <el-header height="70px" style="text-align: right; font-size: 24px; background-color: #061224">Header</el-header>
      <el-container>
        <el-aside width="200px">
          <el-menu
            default-active="GoodsIndex"
            class="el-menu-vertical-demo"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            style="width: 200px"
            router="true">
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>商品管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="AddGoods">新增商品</el-menu-item>
