            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>网站内容管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="2-1">内容分类管理</el-menu-item>
                <el-menu-item index="2-2">内容管理</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>

        </el-aside>
        <el-container>
          <el-main>

            <router-view></router-view>

          </el-main>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    const item = {
      date: '2016-05-02',
      name: '王小虎',
      address: '上海市普陀区金沙江路 1518 弄'
    };
    return {
      tableData: Array(20).fill(item)
    }
  }
}
</script>

<style scoped>
  .content {
    margin: 0px;
    padding: 0px;
  }
</style>



